export default {
	data() {
		return {
			work:[],
			scrollHeight: 0,
			loadingText: '上拉加载更多',
			pagination: 1
		}
	},
	onLoad() {
		var _this = this
		uni.getSystemInfo({
			success(res) {
				_this.scrollHeight = res.windowHeight
			}
		})
		uni.request({
			url:"https://m.jianzhimao.com/ajax/tasks?province=&city=%E5%B9%BF%E5%B7%9E&type=&category_1=&order=4&page="+this.pagination+"&pageSize=20&channel_id=&from=",
			method:"GET",
			dataType:"json",
			success: (res) => {
				this.work = res.data.data
			}
		})
	},
	methods: {
		lower(){
			this.pagination = this.pagination+1
			this.loadingText = '加载中'
			uni.request({
				url:"https://m.jianzhimao.com/ajax/tasks?province=&city=%E5%B9%BF%E5%B7%9E&type=&category_1=&order=4&page="+this.pagination+"&pageSize=20&channel_id=&from=",
				method:"GET",
				dataType:"json",
				success: (res) => {
					
					if (res.data.data.length <= 0) {
						this.loadingText = '我是有底线的'
						return false
					}
					this.work = this.work.concat(res.data.data),
						this.loadingText = '上拉加载更多'
					
					
				}
			})
		}
	}
}
